.logIn {
  display: flex;
  flex-direction: column;
  justify-items: left;
  flex-basis: 50%;
  height: 60%;
  align-content: space-between;

  p {
    font-size: 1.8vw;
    flex-basis: 40%;
  }
  div {
    input:focus {
      outline: none;
    }
    font-size: 1.1vw;
    display: flex;
    height: 80%;
    gap: 20%;
    flex-direction: column;
    width: 90%;
    justify-items: left;
    align-content: space-between;
    input {
      border: none;
      border-bottom: 1px solid #343434;
    }
    button {
      box-sizing: border-box;
      padding: 8px;
      color: #fafafa;
      background-color: #343434;
      border: none;
      border-radius: 3px;
    }
  }
}
